/* =============== */
/* Project Details */
/* =============== */
.projectDetail{background:#fff;overflow:hidden;z-index: 1;position: absolute;top:0;left:0;right:0;}

/*transition styles*/
.projectDetail.fade-enter {opacity:0;transform: translateZ(0);will-change:transform;}
.projectDetail + .projectDetail.fade-enter-active {opacity:1;transition:opacity 0.75s 0.75s;transform: translateZ(0);will-change:transform;}
.projectDetail.fade-enter-done {opacity: 1;transition:opacity 0.75s 1.15s;transform: translateZ(0);will-change:transform;}
.projectDetail.fade-exit{opacity:0;transition:opacity 0.75s 0.75s;transform: translateZ(0);will-change:transform;}

.projectDetail.fade-enter .misc{top:-100%;}
.projectDetail.fade-enter-done .misc{top:0;transition:top 0.75s 0.15s;}
.projectDetail.fade-enter .misc:after{height: 0px;}
.projectDetail.fade-enter-done .misc:after{height:4px; transition:height 0.5s 1s;}
.projectDetail.fade-exit .misc{top:-100%;transition: top 0.75s 0.15s;}

/*project details top bar*/
.projectDetail .misc { position: fixed; top:0; left:0;right:0;margin :auto;z-index: 10;background-color: #000;padding-bottom: 4px; display: flex; justify-content: space-between;}
.projectDetail .misc:after {content:"";position: absolute;left:0;bottom:0; height:4px; background-color: #ff617f; width: 100%;}
.projectDetail .misc .project{cursor: pointer; color:#fff; font-family: "Work Sans", sans-serif; text-transform: uppercase; letter-spacing: 3px;font-size: 1.5rem;display: inline-block;vertical-align: middle;padding:15px;}
.projectDetail .misc .project.next{padding-left:0px;border:0px none;}
.projectDetail .misc .project.prev{padding-right:0px;border:0px none;}
.projectDetail .misc .project img{height: 12px;}
.projectDetail .misc .closeBtn{cursor: pointer; color:#fff; font-family: "Work Sans", sans-serif; text-transform: uppercase; letter-spacing: 3px;font-size: 1.5rem;padding:15px; display: inline-block;vertical-align: middle;border:0px none;}


.projectDetail .detail .bgWrap{height:100vh;overflow:hidden;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-justify-content:flex-end;-ms-flex-pack:end;justify-content:flex-end;-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column;position: relative;}
.projectDetail .detail .bgWrap .bgContent{position: absolute; width: 80%;z-index: 3;top:50%;left:50%;transform:translate(-50%, -50%) translateZ(0);will-change: transform;text-shadow: 0 0 10px #000;}
.projectDetail .detail .bgWrap .bgContent .titleWrap{width:auto;height:165px;}
.projectDetail .detail .bgWrap .bgContent h1.title{font-size:inherit;color:#fff;font-weight: 500; margin:0 0 0px;text-align: center;}
.projectDetail .detail .bgWrap .bgContent h1.title .titleInner{display: inline-block;position: relative; padding-bottom:15px;}
.projectDetail .detail .bgWrap .bgContent h1.title .titleInner:after{content:"";background-color:#fff;display: block; position: absolute;bottom:0; height: 3px;width:75%; margin:auto;left:50%;right:0;box-shadow:0 0 10px 0 #000;min-width:40vw;transform:translateX(-50%) translateZ(0);will-change: transform;}
.projectDetail .detail .bgWrap .bgContent p.slogan{color:#fff;letter-spacing: 3px;font-size: 1.5rem;margin:0; text-transform: uppercase;}
.projectDetail .detail .bgWrap .bgContent p.mainDesc{color:#fff;letter-spacing: 3px;font-size: 2.5rem;margin:20px 0 45px; text-transform: capitalize;}
.projectDetail .detail .bgWrap .bgContent .btn:hover{transition-delay: 0.25s; transition-duration: 0.25s;}
.projectDetail .detail .bgWrap .bgDiv .imgDiv{position: absolute;width: 100%; height: 100%;background-size: cover;background-position:center center; background-repeat: no-repeat;}
.projectDetail .detail .bgWrap .bgDiv img{position:relative;max-width: 100%;display: block;}

.projectDetail .detail .showcase{padding: 0px 0 0;}
.projectDetail .detail .showcase .descLong h2{font-size:3.75rem;margin: 0 auto 25px;}
.projectDetail .detail .showcase .descLong p{font-size:2rem;font-weight: 100;}
.projectDetail .detail .showcase .descLong {width:80%;margin:auto;padding:80px 0 75px;max-width:1260px;}
.projectDetail .detail .bgWrap .scrollDiv{position: absolute;left:0;right:0;margin:auto;bottom:35px;z-index:2;pointer-events: none;opacity: 1;}
.projectDetail .detail .bgWrap .scrollDiv .scrollText{display:inline-block;color:#fff;font-weight: 500;text-transform: uppercase;letter-spacing: 2px;margin:0 0 20px;text-shadow:0 0 10px #000 ;}
.projectDetail .detail .bgWrap .scrollDiv .scrollDown{display:block;max-width: 36px;margin: auto; filter:invert(1);position: relative;animation-name:project-scroll-anim;animation-duration:5s;animation-iteration-count:infinite;animation-timing-function: ease-in-out;}
.projectDetail .detail .bgWrap .scrollDiv .scrollDown img{display:block;max-width: 100%;}
@keyframes project-scroll-anim {
  0%  { top:15px;} 
  50%   {top:-15px;}
  100%  { top:15px;}
}

.projectDetail .showcase .gallery {position:relative;background:#eee;padding:100px 0;box-shadow: 0 -10px 10px -10px inset #dadada;}
.projectDetail .showcase .gallery .firstShowcase{margin: auto;max-width:100%;}
.projectDetail .detail .gallery .firstShowcase img{display:block;margin: 25px auto;border-radius:10px;box-shadow:0 0px 10px 0px #dadada;width:80%;max-width:1260px;}
.projectDetail .detail .gallery .firstShowcase .slick-slide{}
.projectDetail .detail .gallery:hover .firstShowcase .slick-slider .slick-arrow{opacity: 0.75; transition: opacity 0.25s;font-size:0;}
.projectDetail .side-arrows {position: absolute;top:-35px;margin:auto;left:0;right:0;pointer-events:none;} 
.projectDetail .side-arrows .arrow{display: inline-block;max-width:14px;vertical-align:middle;position:relative} 
.projectDetail .side-arrows .arrow img{display:block;max-width:100%;} 
.projectDetail .side-arrows .scroll-text{text-align:center; display: inline-block;vertical-align:middle;margin:0 7px 0 10px;text-transform:uppercase;letter-spacing:3px;font-weight:600} 
.projectDetail .side-arrows .arrow.left{animation-name:gallery-scroll-anim-left;animation-duration:1.75s;animation-iteration-count:infinite;animation-timing-function:linear ;} 
.projectDetail .side-arrows .arrow.right{animation-name:gallery-scroll-anim-right;animation-duration:1.75s;animation-iteration-count:infinite;animation-timing-function: linear;} 

@keyframes gallery-scroll-anim-left {
  0%  { right:3px;} 
  50%   {right:-3px;}
  100%  { right:3px;}
}
@keyframes gallery-scroll-anim-right {
  0%  { left:1px;} 
  50%   {left:-5px;}
  100%  { left:1px;}
}

.projectDetail .detail .showcase .gallery2{position:relative;padding:100px 10%;background-color: #eee;box-shadow: 0 0 10px 0 inset #dadada;}
.projectDetail .detail .showcase .gallery2 .gallery-item{position:relative;max-width:53%;box-shadow:0 0 50px 0px #bbb;border-radius:5px;overflow:hidden; z-index:2;}
.projectDetail .detail .showcase .gallery2 .gallery-item + .gallery-item{z-index:1;margin-left:auto;margin-top:-30px;}
.projectDetail .detail .showcase .gallery2 .gallery-item img{max-width: 100%;display: block;}

.projectDetail .detail .showcase .gallery2.mobile {display:flex;justify-content: center;}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item{position:static;max-width:none;display:inline-block;margin-right:25px;box-shadow:none;overflow:visible;}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item + .gallery-item{margin:0 25px}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item + .gallery-item:last-child{margin-right:0px;margin-left:25px;}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item img{max-width:none;display:block;max-height:100%;margin-left:auto;box-shadow:0 0 50px 0px #bbb;border-radius:5px;max-height:550px;max-height: 55vw;}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item + .gallery-item img{margin:auto;}
.projectDetail .detail .showcase .gallery2.mobile .gallery-item + .gallery-item:last-child img{margin-top:0;margin-right:auto;margin-left:0;}



.projectDetail .detail .showcase .descExt{width:90%;margin:auto;padding: 75px 0 50px;text-align: center;display:flex;max-width: 1920px;}
.projectDetail .detail .showcase .descExt .descRole,
.projectDetail .detail .showcase .descExt .descStack{flex:1;text-align: center;vertical-align: top;font-size:1.25rem;font-weight: 100;padding:0px 0;}
.projectDetail .detail .showcase .descExt .descStack .stackWrap{padding-left:55px;}
.projectDetail .detail .showcase .descExt h2{font-size:3rem;margin:0;}
.projectDetail .detail .showcase .descExt p.note{color:#dadada;font-family:"Work Sans", sans-serif;text-transform: capitalize;margin:0;}
.projectDetail .detail .showcase .descExt .descStack .stackHead{margin:0 0 30px;}
.projectDetail .detail .showcase .descExt .descStack .stackBase{display: flex;flex-wrap: wrap;justify-content:center;max-width:575px;margin:auto;padding-left: 50px;}
.projectDetail .detail .showcase .descExt .descStack .stackBase > div{flex:1 1 50%;margin:0 auto 0px;text-align: left;margin:0 0 25px;}
.projectDetail .detail .showcase .descExt .descStack .stackBase h3{font-weight: 500;font-size:1.75rem;font-family: "Open Sans", sans-serif;margin: 0px 0 5px;}
.projectDetail .detail .showcase .descExt .descStack .stackBase p{font-weight: 100;font-size:1.25rem;margin: 5px 0 0px;letter-spacing: 2px;}
.projectDetail .detail .showcase .descExt .descStack .stackBase > div{padding-right:25px}
.projectDetail .detail .showcase .descExt .descStack .stackBase > div:nth-child(n+5){padding-right:0px}

.projectDetail .detail .showcase .descExt .descRole{border-right:1px solid #ffcbd4;}
.projectDetail .detail .showcase .descExt .descRole .roleHead{margin:0 0 30px;}
.projectDetail .detail .showcase .descExt .descRole .roleBase > div{margin: 0 0 45px;}
.projectDetail .detail .showcase .descExt .descRole .roleBase span.feat{font-size: 2rem;font-weight: 500;text-transform: uppercase;}
.projectDetail .detail .showcase .descExt .descRole .roleBase p{text-transform: uppercase; letter-spacing: 3px;margin:0;}
.projectDetail .detail .showcase .descExt .descRole .roleBase p span{display: inline-block;vertical-align: middle;}

.projectDetail .detail .showcase .descExt + .keyTakeaway{padding:25px 0 100px;}

.projectDetail .detail .showcase .keyTakeaway{padding:100px 0;margin:auto;}
.projectDetail .detail .showcase .keyTakeaway .keyTakeaway-inner{width:80%;max-width:1260px;margin:auto;}
.projectDetail .detail .showcase .keyTakeaway h2{font-size:3rem;}
.projectDetail .detail .showcase .keyTakeaway p{font-size: 1.75rem;}


